"use client";
import { SignedIn, SignedOut, UserButton, SignInButton } from "@clerk/nextjs";

export default function AuthNav() {
  return (
    <nav className="flex justify-end p-4 border-b">
      <SignedOut>
        <SignInButton />
      </SignedOut>
      <SignedIn>
        <UserButton afterSignOutUrl="/" />
      </SignedIn>
    </nav>
  );
}
